import {Component} from '@angular/core';
import {IonicPage, NavController} from 'ionic-angular';

import {TransactionService} from '../../providers/transaction-service-mock';

@IonicPage({
	name: 'page-transactions',
	segment: 'transactions'
})

@Component({
    selector: 'page-transactions',
    templateUrl: 'transaction-list.html'
})

export class TransactionListPage {

    transactions: Array<any> = [];

    constructor(public navCtrl: NavController, public service: TransactionService) {
        this.getTransactions();
        // console.log(this.messages);
    }

    itemTapped() {
      // console.log('itemTapped: ', message);
      // this.navCtrl.push(InvoiceDetailPage, invoice);
      this.navCtrl.push('page-checkout');
    }

    getTransactions() {
      this.service.getTransactions().then(data => this.transactions = data);
    }

}



